'use client';

import React, { useState, useEffect } from 'react';
import { StockPriceCard, StockPriceCardSkeleton } from '@/components/dashboard/StockPriceCard';
import { Navigation } from '@/components/layout/Navigation';
import { Search } from '@/components/ui/Search';
import { Button } from '@/components/ui/Button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/Card';
import { formatCurrency, formatPercentage, getChangeColorClass } from '@/lib/utils';

/**
 * Enhanced Stock Dashboard with 52-Week Data
 * 
 * WHY this enhanced dashboard:
 * - Integrates with historical API for comprehensive data
 * - Displays 52-week high/low information
 * - Shows stock performance over different time periods
 * - Provides detailed stock analysis capabilities
 * - Demonstrates real-world stock dashboard features
 */

interface HistoricalDataPoint {
  date: string;
  open: number;
  high: number;
  low: number;
  close: number;
  volume: number;
}

interface StockData {
  symbol: string;
  companyName: string;
  currentPrice: number;
  currentChange: number;
  currentChangePercent: number;
  week52High: number;
  week52Low: number;
  week52HighDate: string;
  week52LowDate: string;
  historicalData: HistoricalDataPoint[];
  dataPoints: number;
  period: string;
}

interface MarketSummary {
  totalStocks: number;
  gainers: number;
  losers: number;
  unchanged: number;
  totalVolume: number;
  totalMarketCap: number;
}

export default function EnhancedDashboardPage() {
  const [stocks, setStocks] = useState<StockData[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedPeriod, setSelectedPeriod] = useState('1y');
  const [searchQuery, setSearchQuery] = useState('');

  // Company symbols for the dashboard
  const companySymbols = [
    'RELIANCE', 'TCS', 'HDFCBANK', 'INFY', 'ICICIBANK', 
    'HINDUNILVR', 'ITC', 'SBIN'
  ];

  // Fetch stock data from historical API
  const fetchStockData = async (symbol: string, period: string): Promise<StockData> => {
    try {
      const response = await fetch(`/api/stocks/${symbol}/historical?period=${period}`);
      if (!response.ok) {
        throw new Error(`Failed to fetch data for ${symbol}`);
      }
      return await response.json();
    } catch (error) {
      console.error(`Error fetching data for ${symbol}:`, error);
      throw error;
    }
  };

  // Load all stock data
  useEffect(() => {
    const loadStockData = async () => {
      try {
        setLoading(true);
        setError(null);

        // Fetch data for all companies in parallel
        const stockPromises = companySymbols.map(symbol => 
          fetchStockData(symbol, selectedPeriod)
        );

        const stockData = await Promise.all(stockPromises);
        setStocks(stockData);
      } catch (err) {
        console.error('Error loading stock data:', err);
        setError('Failed to load stock data. Please try again.');
      } finally {
        setLoading(false);
      }
    };

    loadStockData();
  }, [selectedPeriod]);

  // Calculate market summary
  const marketSummary: MarketSummary = stocks.reduce((summary, stock) => {
    summary.totalStocks++;
    summary.totalVolume += stock.historicalData[stock.historicalData.length - 1]?.volume || 0;
    
    if (stock.currentChange > 0) summary.gainers++;
    else if (stock.currentChange < 0) summary.losers++;
    else summary.unchanged++;

    return summary;
  }, {
    totalStocks: 0,
    gainers: 0,
    losers: 0,
    unchanged: 0,
    totalVolume: 0,
    totalMarketCap: 0,
  });

  // Filter stocks based on search query
  const filteredStocks = stocks.filter(stock =>
    stock.symbol.toLowerCase().includes(searchQuery.toLowerCase()) ||
    stock.companyName.toLowerCase().includes(searchQuery.toLowerCase())
  );

  // Period options for time range selection
  const periodOptions = [
    { value: '1m', label: '1 Month' },
    { value: '3m', label: '3 Months' },
    { value: '6m', label: '6 Months' },
    { value: '1y', label: '1 Year' },
  ];

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
      <Navigation />
      
      <main className="container mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header Section */}
        <div className="mb-8">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
                Enhanced Stock Dashboard
              </h1>
              <p className="mt-2 text-gray-600 dark:text-gray-400">
                Comprehensive stock analysis with 52-week data and historical trends
              </p>
            </div>
            
            {/* Period Selector */}
            <div className="mt-4 lg:mt-0 flex items-center space-x-4">
              <label className="text-sm font-medium text-gray-700 dark:text-gray-300">
                Time Period:
              </label>
              <select
                value={selectedPeriod}
                onChange={(e) => setSelectedPeriod(e.target.value)}
                className="px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm bg-white dark:bg-gray-700 text-gray-900 dark:text-white focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              >
                {periodOptions.map((option) => (
                  <option key={option.value} value={option.value}>
                    {option.label}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>

        {/* Market Summary Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardContent className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-blue-100 dark:bg-blue-900 rounded-lg">
                  <svg className="w-6 h-6 text-blue-600 dark:text-blue-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
                  </svg>
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Total Stocks</p>
                  <p className="text-2xl font-bold text-gray-900 dark:text-white">{marketSummary.totalStocks}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-green-100 dark:bg-green-900 rounded-lg">
                  <svg className="w-6 h-6 text-green-600 dark:text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 10l7-7m0 0l7 7m-7-7v18" />
                  </svg>
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Gainers</p>
                  <p className="text-2xl font-bold text-green-600 dark:text-green-400">{marketSummary.gainers}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-red-100 dark:bg-red-900 rounded-lg">
                  <svg className="w-6 h-6 text-red-600 dark:text-red-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
                  </svg>
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Losers</p>
                  <p className="text-2xl font-bold text-red-600 dark:text-red-400">{marketSummary.losers}</p>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="p-6">
              <div className="flex items-center">
                <div className="p-2 bg-gray-100 dark:bg-gray-700 rounded-lg">
                  <svg className="w-6 h-6 text-gray-600 dark:text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7h8m0 0v8m0-8l-8 8-4-4-6 6" />
                  </svg>
                </div>
                <div className="ml-4">
                  <p className="text-sm font-medium text-gray-600 dark:text-gray-400">Total Volume</p>
                  <p className="text-2xl font-bold text-gray-900 dark:text-white">
                    {(marketSummary.totalVolume / 1000000).toFixed(1)}M
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Search and Filters */}
        <div className="mb-6">
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
            <Search
              placeholder="Search stocks by symbol or company name..."
              value={searchQuery}
              onChange={setSearchQuery}
              className="max-w-md"
            />
            <div className="text-sm text-gray-600 dark:text-gray-400">
              Showing {filteredStocks.length} of {stocks.length} stocks
            </div>
          </div>
        </div>

        {/* Error State */}
        {error && (
          <Card className="mb-6 border-red-200 dark:border-red-800">
            <CardContent className="p-6">
              <div className="flex items-center">
                <svg className="w-5 h-5 text-red-600 dark:text-red-400 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                <span className="text-red-600 dark:text-red-400">{error}</span>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Stock Cards Grid */}
        {loading ? (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {Array.from({ length: 8 }).map((_, index) => (
              <StockPriceCardSkeleton key={index} variant="detailed" />
            ))}
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
            {filteredStocks.map((stock) => (
              <StockPriceCard
                key={stock.symbol}
                data={{
                  symbol: stock.symbol,
                  name: stock.companyName,
                  price: stock.currentPrice,
                  change: stock.currentChange,
                  changePercent: stock.currentChangePercent,
                  volume: stock.historicalData[stock.historicalData.length - 1]?.volume || 0,
                  week52High: stock.week52High,
                  week52Low: stock.week52Low,
                  week52HighDate: stock.week52HighDate,
                  week52LowDate: stock.week52LowDate,
                }}
                variant="detailed"
                onClick={() => {
                  // Navigate to detailed stock view
                  console.log(`Viewing details for ${stock.symbol}`);
                }}
              />
            ))}
          </div>
        )}

        {/* Empty State */}
        {!loading && !error && filteredStocks.length === 0 && (
          <Card>
            <CardContent className="p-12 text-center">
              <svg className="w-12 h-12 text-gray-400 mx-auto mb-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
              </svg>
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
                No stocks found
              </h3>
              <p className="text-gray-600 dark:text-gray-400">
                Try adjusting your search criteria or browse all available stocks.
              </p>
            </CardContent>
          </Card>
        )}
      </main>
    </div>
  );
}
